export default {
  props: {
    content: {
      type: String,
      default: null,
    },
  },
  data: () => ({}),
  created() {},
  computed: {
    markdown() {
      try {
        return this.$markdown(this.content);
      } catch (error) {
        console.log(error);
      }
      return null;
    },
  },
  methods: {},

